<template>
  <div class="home">
    <!-- <div class="top-tab">
      <i class="iconfont icongengduo-2"></i>
      <h1>首页推荐</h1>
      <i class="iconfont iconsearch"></i>
    </div> -->
    <top-tab></top-tab>

    <!-- <van-nav-bar fixed z-index="100" title="首页" left-text="" left-arrow
      @click-left="showPopup">
      <template #right>
        <van-icon name="search" size="18" />
      </template>
    </van-nav-bar> -->

    <!-- <van-tabs v-model="active" animated sticky swipeable color="#4fc08d">
      <van-tab v-for="index in 2" :title="'选项 ' + index">
       
      </van-tab>
    </van-tabs> -->

    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-grid :border="false" :column-num="2">
          <van-grid-item v-for="item in list" :key="item" :title="item">
            <div class="live-item">
              <div class="live-img">
                <img src="https://img.yzcdn.cn/vant/apple-1.jpg" alt="封面图">
                <div class="live-img-info">
                  <div class="live-info-wrapper">
                    <span class="live-type">英雄联盟</span>
                    <span class="live-popularity">
                      <i class="iconfont iconuser"></i>
                      <span>419万</span>
                    </span>
                  </div>
                </div>

                <div class="live-title">
                  <i class="iconfont iconlike"></i>
                  <div class="user-info">
                    <span class="user-name">斗鱼鲍鲍</span>
                    <span class="user-title">直播了</span>
                  </div>
                </div>
              </div>
            </div>
          </van-grid-item>
        </van-grid>
        <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
      </van-list>
    </van-pull-refresh>

  </div>
  
</template>

<script>

import { mapState, mapMutations } from "vuex";
import Swiper from "swiper";
import {log} from '../utils/log.js'
export default {
  name: "home",
  components: {
    // SideBar,
  },
  data() {
    return {
      active: 0,
      swiperOption: {
        slidesPerView: "auto",
        centeredSlides: true,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          type: "fraction"
        },
        loop: true,
        observer: true, //修改swiper自己或子元素时，自动初始化swiper
        observeParents: true //修改swiper的父元素时，自动初始化swiper
      },

      list: [],
      loading: false,
      finished: false,
      refreshing: false,

      isShowSetPage: false,

    };
  },
  created() {
    let path = this.$route.path
    let isShowSetPage = this.home.isShowSetPage
    this.isShowSetPage = isShowSetPage
    this.setState({'path': path})
    this.setState({'isShowSetPage': false})
  },
  watch: {
    
  },
  computed: {
      ...mapState({
        home: state => state.home
      }),
      // carXianLen() {
      //     return (
      //         this.completed.length +
      //         this.complet.length +
      //         this.processing.length
      //     );
      // }
  },
  watch: {
    // cityname: function() {
    //   log('watch cityname this.home.city ', `(${this.cityname})`, `(${this.home.city})`)
    //   let area = this.getcitycode(this.cityname)
    //   this.area = area
    //   this.setState({'area': this.area})
    // },
  },

  activated() {

  },
  
  mounted() {
    var mySwiper = new Swiper(".swiper-container", {
      direction: "horizontal",
      loop: false,
      slidesPerView: "auto",
      centeredSlides: true,
      spaceBetween: 20,
      observer: true,
      observeParents: true
    });
  },
  methods: {
    ...mapMutations(["setState"]),
    // getcityname(citycode) {
    //   for (const i in this.citylist) {
    //     if (citycode == this.citylist[i].code) {
    //       this.cityname = this.citylist[i].name;
    //     }
    //   }
    // },
    // showPopup() {
    //   this.isShowSetPage = true;
    //   this.$router.push('/')
    // },
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 5; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 8) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
  },
  
};
</script>

<style lang="scss" scoped>

  .top-tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    background-color: aquamarine;
  }

  .live-item {
    .live-img {
      background-color: burlywood;
      position: relative;
      img {
        height: 100px;
        width: 190px;
      }
      
         
      .live-img-info {
        position: absolute;
        bottom: 42px;
        padding: 0 6px;
        width: 100%;
        .live-info-wrapper {
          display: flex;
          justify-content: space-between;
          font-size: 10px;
          color: #ffffff;
          .live-type {

          }
          .live-popularity {
            display: flex;
            align-items: center;
            .iconuser {
              font-size: 10px;
            }
          }
        }
      }
      .live-title {
        display: flex;
        align-items: center;
        margin: 0 4px;
        .iconfont {
          font-size: 22px;
        }
        .user-info {
          display: flex;
          flex-direction: column;
          margin-left: 4px;
          .user-name {
            font-size: 12px;
          }
          .user-title {
            font-size: 10px;
          }
        }
      }
    }
  }
  .home {
    background-color: #eeeeee;
    /deep/ .van-nav-bar::after {
      border: unset;
    }
  }
  /deep/ .van-list {
    margin-top: 20px;
  }
  /deep/ .van-grid-item__content {
    padding: 4px;
    background-color: transparent;
  }
  /deep/ .van-tabs__wrap {
    height: 40px;
    bottom: -15px;
  }
  /deep/ .van-tab {
    line-height: 40px;
  }
  /deep/ .van-sticky--fixed {
    margin-top: 36px;
  }
  /deep/ .van-sticky {
    /deep/ .van-tabs__wrap::after {
      border: unset;
    }
  }
  
  /deep/ .van-icon {
    color: #8b8b8b;
  }
</style>
